<template>
    <p class="error" v-if="error">{{ error }}</p>

    <div
        v-for="banner in banners"
        :key="banner._id"
        class="border rounded p-5 bg-gray-100 shadow divide-y"
    >
        <BannerForm
            v-if="edit == banner._id"
            :banner="banner"
            :guardando="guardando"
            @close="edit = ''"
            @guardar="actualizarBanner"
        />

        <div v-else class="text-center">
            <h3 class="text-xl mb-2">{{ banner.tituloBanner }}</h3>

            <img
                class="mb-2 mx-auto rounded-lg"
                :src="banner.imagenBanner.url"
                :alt="banner.tituloBanner"
            />

            <a :href="banner.urlBanner">{{ banner.urlBanner }}</a>

            <div class="flex justify-end pt-3">
                <EditIcon
                    color="gold"
                    class="cursor-pointer ml-2"
                    @click="edit = banner._id"
                />
                <DeleteIcon
                    color="red"
                    :class="[
                        'ml-2',
                        eliminando == banner._id
                            ? 'animate-bounce'
                            : 'cursor-pointer',
                    ]"
                    @click="borrarBanner(banner._id)"
                />
            </div>
        </div>
    </div>
</template>

<script>
import BannerService from './BannerService';
import BannerForm from './BannerForm';
import DeleteIcon from '../svg/DeleteIcon';
import EditIcon from '../svg/EditIcon';

export default {
    name: 'BannerList',
    components: {
        BannerForm,
        EditIcon,
        DeleteIcon,
    },
    props: {
        banners: Array,
    },
    emits: ['cargarBanner'],
    data() {
        return {
            error: '',
            edit: '',
            guardando: false,
            eliminando: '',
        };
    },
    methods: {
        async actualizarBanner(data) {
            this.guardando = true;
            await BannerService.update(this.edit, data);
            this.guardando = false;
            this.$emit('cargarBanner');
        },
        async borrarBanner(id) {
            this.eliminando = id;
            await BannerService.delete(id);
            this.eliminando = '';
            this.$emit('cargarBanner');
        },
    },
};
</script>
